/* eslint-disable no-restricted-globals */
/* eslint-disable camelcase */
import { sentenceCase, capitalCase } from 'change-case';
import { useState, useEffect, } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import { useSnackbar } from 'notistack';

// @mui
import { useTheme } from '@mui/material/styles';
import {
  Card,
  Table,
  Avatar,
  Button,
  Checkbox,
  TableRow,
  TableBody,
  TableCell,
  Container,
  Typography,
  TableContainer,
  TablePagination,
  Switch,
  Tabs,
  Tab,
  Backdrop,
  CircularProgress
} from '@mui/material';
// swal 
import swal from 'sweetalert';
// format
import FormControlLabel from '@mui/material/FormControlLabel';
// routes
import { PATH_ADMIN } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import Label from '../../components/Label';
import Iconify from '../../components/Iconify';
import Scrollbar from '../../components/Scrollbar';
import SearchNotFound from '../../components/SearchNotFound';

import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
// format number 
import { fCurrency, } from '../../utils/formatNumber';
// sections
import { UserListHead, PlanToolbar, PlanMoreMenu } from '../../sections/admin/user/list';


// axios

import axios from '../../utils/axios';

// ----------------------------------------------------------------------

const TABLE_HEAD = [
  { id: 'added_by', label: 'Added By', alignRight: false },
  { id: 'cable_name', label: 'Cable Name', alignRight: false },
  { id: 'plan_name', label: 'Plan Name', alignRight: false },
  { id: 'plan_price', label: 'Plan Price', alignRight: false },
  { id: 'plan_id', label: 'Plan ID', alignRight: false },
  { id: 'status', label: 'status', alignRight: false },
];
const ACCOUNT_TABS = [
  {
    value: 'ALL',
    label: 'ALL'
  },
  {
    value: 1,
    label: 'Active'
  },
  {
    value: 0,
    label: 'Deactivated'
  },
];
// ----------------------------------------------------------------------

export default function CablePlan() {
  const theme = useTheme();
  const { themeStretch } = useSettings();
  const [userList, setUserList] = useState([]);
  const [page, setPage] = useState(0);
  const [order, setOrder] = useState('asc');
  const [selected, setSelected] = useState([]);
  const [orderBy, setOrderBy] = useState('id');
  const [filterName, setFilterName] = useState('');
  const [rowsPerPage, setRowsPerPage] = useState(5);
  const [dense, Setdense] = useState(false);
  const [status, setStatus] = useState('ALL');
  const { enqueueSnackbar } = useSnackbar();
  const AccessToken = window.localStorage.getItem('accessToken');
  const [load, SetLoad] = useState(true)

  useEffect(() => {
    const initialize = async () => {

      await axios.get(`/api/all/system/plan/purchase/by/habukhan/${AccessToken}/secure`).then(data => {
        setUserList(data.data?.cable_plans);
        SetLoad(false)
      }).catch(() => SetLoad(false));
    }
    initialize();
  }, [AccessToken]);

  const handleRequestSort = (property) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  const handleSelectAllClick = (checked) => {
    if (checked.target.checked) {
      const newSelecteds = userList.map((n) => n.plan_id);
      setSelected(newSelecteds);
      return;
    }
    setSelected([]);
  };

  const handleClick = (username) => {
    const selectedIndex = selected.indexOf(username);
    let newSelected = [];
    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, username);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1));
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(
        selected.slice(0, selectedIndex),
        selected.slice(selectedIndex + 1),
      );
    }
    setSelected(newSelected);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const handleFilterByName = (filterName) => {
    setFilterName(filterName);
    setPage(0);
  };
  const handleDeleteMultiUser = (selected) => {
    swal({
      title: "Note",
      text: `Are You Sure You Want To Delete plans`,
      icon: "info",
      buttons: true,
      dangerMode: true,
    })
      .then((willDelete) => {
        if (willDelete) {
          axios.post(`/api/delete/cable/habukhan/plans/hacker/${AccessToken}/system`, {
            plan_id: selected
          }).then(data => {
            enqueueSnackbar(data.data.message)
            const deleteUsers = userList.filter((user) => !selected.includes(user.plan_id));
            setSelected([]);
            setUserList(deleteUsers);
          }).catch(error => {
            if (error.data?.message) {
              enqueueSnackbar(error.data.message, { variant: 'error' })
            } else {
              enqueueSnackbar('An Error Occured', { variant: 'error' })
            }
          });
        } else {
          swal("Request Cancelled");
        }
      });
  };

  const emptyRows = page > 0 ? Math.max(0, (1 + page) * rowsPerPage - userList.length) : 0;

  const filteredUsers = applySortFilter(userList, getComparator(order, orderBy), filterName, status);



  const isNotFound = !filteredUsers.length && Boolean(filterName);


  function handleChangeDense(event) {
    Setdense(event.target.checked);
  }

  return (
    <Page title="Plans: Cable Data">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading="Cable Plans"
          links={[
            { name: 'Dashboard', href: PATH_ADMIN.root },
            { name: 'Add Plan', href: PATH_ADMIN.plan.newcable },
            { name: 'List' },
          ]}
          action={
            <Button
              variant="contained"
              component={RouterLink}
              to={PATH_ADMIN.plan.newcable}
              startIcon={<Iconify icon={'eva:plus-fill'} />}
            >
              New Cable Plan
            </Button>
          }
        />

        <Card>
          <Tabs
            value={status}
            scrollButtons="auto"
            variant="scrollable"
            allowScrollButtonsMobile
            onChange={(e, value) => setStatus(value)}
          >
            {ACCOUNT_TABS.map((tab) => (
              <Tab disableRipple key={tab.value} label={capitalCase(tab.label)} value={tab.value} />
            ))}
          </Tabs>
          <PlanToolbar
            numSelected={selected.length}
            filterName={filterName}
            onFilterName={handleFilterByName}
            onDeleteUsers={() => handleDeleteMultiUser(selected)}
          />

          <Scrollbar>
            <TableContainer sx={{ minWidth: 800 }}>
              <Table
                size={dense ? 'small' : 'medium'}
              >
                <UserListHead
                  order={order}
                  orderBy={orderBy}
                  headLabel={TABLE_HEAD}
                  rowCount={userList.length}
                  numSelected={selected.length}
                  onRequestSort={handleRequestSort}
                  onSelectAllClick={handleSelectAllClick}
                  onDeleteUsers={() => handleDeleteMultiUser(selected)}
                />

                <TableBody>
                  {filteredUsers.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((row) => {
                    const { plan_name, added_by, plan_price, cable_name, plan_id, plan_status, profile_image } = row;
                    const isItemSelected = selected.indexOf(plan_id) !== -1;
                    return (
                      <TableRow
                        hover
                        key={plan_id}
                        tabIndex={-1}
                        role="checkbox"
                        selected={isItemSelected}
                        aria-checked={isItemSelected}
                      >
                        <TableCell>
                          <Checkbox checked={isItemSelected} onClick={() => handleClick(plan_id)} />
                        </TableCell>

                        <TableCell sx={{ display: 'flex', alignItems: 'center' }}>
                          <Avatar alt={added_by} src={profile_image} sx={{ mr: 2 }} />
                          <Typography variant="subtitle2" noWrap>
                            {added_by}
                          </Typography>
                        </TableCell>
                        <TableCell align="left">{cable_name}</TableCell>
                        <TableCell align="left">{plan_name}</TableCell>
                        <TableCell align="left">â‚¦{fCurrency(plan_price)}</TableCell>
                        <TableCell align="left">{plan_id}</TableCell>
                        <TableCell align="left">
                          <Label
                            variant={theme.palette.mode === 'light' ? 'ghost' : 'filled'}
                            color={(plan_status === 1 ? 'success' : 'warning')}
                          >
                            {sentenceCase((plan_status === 0 && 'deactivated') || (plan_status === 1 && 'active'))}
                          </Label>
                        </TableCell>

                        <TableCell align="right">
                          <PlanMoreMenu userName={plan_id.toString()} route='editcable' />
                        </TableCell>
                      </TableRow>
                    );
                  })}
                  {emptyRows > 0 && (
                    <TableRow style={{ height: 53 * emptyRows }}>
                      <TableCell colSpan={6} />
                    </TableRow>
                  )}

                </TableBody>
                {isNotFound && (

                  <TableRow>
                    <TableCell align="center" colSpan={5} sx={{ py: 3 }}>
                      <SearchNotFound searchQuery={filterName} />
                    </TableCell>
                  </TableRow>

                )}
              </Table>
            </TableContainer>
          </Scrollbar>

          <FormControlLabel
            control={<Switch checked={dense} onChange={(event) => handleChangeDense(event)} />}
            label="Dense padding" align="left"
          />

          <TablePagination
            rowsPerPageOptions={[5, 10, 25]}
            component="div"
            count={userList.length}
            rowsPerPage={rowsPerPage}
            page={page}
            onPageChange={(e, page) => setPage(page)}
            onRowsPerPageChange={handleChangeRowsPerPage}
          />

        </Card>
        <Backdrop
          sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
          open={load}
        >
          <CircularProgress color="inherit" />
        </Backdrop>
      </Container>
    </Page>
  );
}

// ----------------------------------------------------------------------

function descendingComparator(a, b, orderBy) {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }
  if (b[orderBy] > a[orderBy]) {
    return 1;
  }
  return 0;
}

function getComparator(order, orderBy) {
  return order === 'asc'
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy);
}




function applySortFilter(array, comparator, query, status) {
  const stabilizedThis = array.map((el, index) => [el, index]);
  stabilizedThis.sort((a, b) => {
    const order = comparator(a[0], b[0]);
    if (order !== 0) return order;
    return a[1] - b[1];
  });
  if (!query && status === 'ALL') {
    return stabilizedThis.map((el) => el[0]);
  }
  if (!query && status !== 'ALL') {
    return array.filter((_user) => _user.plan_status === status);
  }
  if (query && status === 'ALL') {
    return array.filter((_user) => (_user.plan_id === query || _user.plan_name.toString().toLowerCase().indexOf(query.toLowerCase()) !== -1 || _user.added_by.toString().toLowerCase().indexOf(query.toLowerCase()) !== -1 || _user.cable_name.toLowerCase().indexOf(query.toLowerCase()) !== -1));
  }
  if (query && status !== 'ALL') {
    return array.filter((_user) => (_user.plan_id === query || _user.plan_name.toString().toLowerCase().indexOf(query.toLowerCase()) !== -1 || _user.added_by.toString().toLowerCase().indexOf(query.toLowerCase()) !== -1 || _user.cable_name.toLowerCase().indexOf(query.toLowerCase()) !== -1) && _user.plan_status === status);
  }
  return stabilizedThis.map((el) => el[0]);
}

